
.main-banner {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
  &__wrap {
    background-color: $background-body;
  }

  .hamburger {
    display: none;
  }

  .logo {
    display: flex;
    align-items: center;

    &:hover,
    &:focus {
      text-decoration: none;
    }

    &--wrap {
      flex: 0 0 357px;
    }
    &__image {
      margin-right: 15px;
    }
    &__title {
      text-transform: uppercase;
      font-size: 18px;
      font-weight: bold;
      margin-bottom: 3px;
    }
    &__description {
      font-size: 14px;
      line-height: 1.25;
      color: $default-text-color;
    }
  }

  .datetime {
    display: flex;
    align-items: center;
    flex: auto;
    justify-content: center;

    .datetime__day,
    .datetime__day-of-week,
    .datetime__clock {
      transition: color $transition-time/2 ease-in;
    }

    &.uninitialized {
      .datetime__day,
      .datetime__day-of-week,
      .datetime__clock {
        color: transparent !important;
      }
    }

    &__calendar {
      margin-right: 15px;
    }

    &__day {
      color: $text-color;
      font-size: 20px;
      line-height: 0.9;
    }

    &__day-of-week {
      font-size: 14px;
      color: $gray-transparent-color;
    }

    &__clock {
      box-sizing: border-box;
      font-size: 20px;
      padding: 11px 20px;
      border-radius: 100px;
      box-shadow: inset 0 0 3px 0 rgba(0, 0, 0, 0.22);
      color: $text-color;
      flex: 0 0 191px;
      max-width: 191px; //IE fix
    }

    &__timezone {
      line-height: 1.25;
      flex: 0;
      font-size: 14px;
      color: $gray-transparent-color;
      margin-left: 15px;
      @include media('<1201px') {
        display: none;
      }
    }
  }

  .actions {
    display: flex;
    flex-flow: row nowrap;
    flex: none;
    justify-content: flex-end;
    align-items: center;

    > * + * {
      margin-left: 10px;
      cursor: pointer;
    }

    &__locale {

      .dropdown__button {
        border: none;
        color: $text-color;
        min-width: 93px;
        padding-right: 10px;
        &::after {
          background-image: url(../../images/header/ln_arrow_down.png);
        }
      }
    }

    .button--icon {
      padding: 13px 10px;
      box-shadow: none;
      border-color: transparent;

      svg {
        width: 100%;
        height: 100%;
      }
    }

    .dropdown {
      &__item {
        padding: 10px 22px;
      }
    }
  }
}
