.simple-radio-group {
    & > input {
        position: absolute;
        width: 1px;
        height: 1px;
        margin: -1px;
        overflow: hidden;
    }

    & > label {
        display: block;
        position: relative;
        padding-left: 32px;
        line-height: 22px;
        margin-bottom: 20px;
        color: $text-color;
        font-size: $inner-title-font-size;
        font-family: $default-font;
        cursor: pointer;
    }

    & > label::before {
        content: '';
        box-sizing: border-box;
        position: absolute;
        width: 22px;
        height: 22px;
        border-radius: 50%;
        border: 1px solid $border-color;
        left: 0;
    }

    & > label::after {
        content: '';
        position: absolute;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        display: none;
        background-color: $accent-color;
        left: 5px;
        top: 5px;
    }

    & > input:checked + label {
    }

    & > input:checked + label::before {
        border-color: $accent-color;
    }

    & > input:checked + label::after {
        display: block;
    }
}
