.radio-group {
  box-shadow: 0 2px 12px 0 rgba(212, 212, 214, 0.7);
  border: solid 1px rgba(212, 212, 214, 0.7);
  background-color: #fff;
  border-radius: 24px;
  overflow: hidden;
  display: inline-block;
}

.radio-group > label {
  height: 48px;
  line-height: 48px;
  padding: 0 20px;
  margin: 0;
  border: none;
  background-color: #fff;
  color: #2c3756;
  font-weight: bold;
  font-family: "PT Sans", sans-serif;
  font-size: 24px;
  display: inline-block;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.radio-group > input {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.radio-group > input:checked + label,
.radio-group .radio-group > label:hover,
.radio-group .radio-group > label:focus {
  border: none;
  background-color: #fff;
  color: #2c3756;
  box-shadow: none;
}

.radio-group > input:checked + label:hover,
.radio-group > input:checked + label:focus {
  background-color: #2aac4a;
  border: none;
}

.radio-group > input:checked + label {
  border-radius: 24px;
  background-color: #2aac4a;
  color: #fff;
}

.simple-radio-group > input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
}

.simple-radio-group > label {
  display: block;
  position: relative;
  padding-left: 32px;
  line-height: 22px;
  margin-bottom: 20px;
  color: #2c3756;
  font-size: 20px;
  font-family: "PT Sans", sans-serif;
  cursor: pointer;
}

.simple-radio-group > label::before {
  content: '';
  box-sizing: border-box;
  position: absolute;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  border: 1px solid #d4d4d6;
  left: 0;
}

.simple-radio-group > label::after {
  content: '';
  position: absolute;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  display: none;
  background-color: #2aac4a;
  left: 5px;
  top: 5px;
}

.simple-radio-group > input:checked + label::before {
  border-color: #2aac4a;
}

.simple-radio-group > input:checked + label::after {
  display: block;
}

