  1% {
    display: block;
    opacity: 0;
    z-index: 5;
  }

  25% {
    display: block;
    opacity: .25;
    z-index: 5;
  }

  50% {
    display: block;
    opacity: .5;
    z-index: 5;
  }

  75% {
