.radio-group {
  box-shadow: 0 2px 12px 0 $border-separator-color;
  border: solid 1px $border-separator-color;
  background-color: $background-body;
  border-radius: 24px;
  overflow: hidden;
  display: inline-block;

  & > label {
    height: 48px;
    line-height: 48px;
    padding: 0 20px;
    margin: 0;
    border: none;
    background-color: $background-body;
    color: $text-color;
    font-weight: bold;
    font-family: $default-font;
    font-size: $title-font-size;
    display: inline-block;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  & > input {
     border: 0;
     clip: rect(0 0 0 0);
     height: 1px;
     margin: -1px;
     overflow: hidden;
     padding: 0;
     position: absolute;
     width: 1px;
  }

  & > input:checked + label,
    .radio-group > label:hover,
    .radio-group > label:focus {
      border: none;
      background-color: $background-body;
      color: $text-color;
      box-shadow: none;
  }

  & > input:checked + label:hover,
  & > input:checked + label:focus {
    background-color: $accent-color;
    border: none;
  }

  & > input:checked + label {
    border-radius: 24px;
    background-color: $accent-color;
    color: $background-body;
  }
}
