.side-nav {
    padding: 30px 20px;
    background-color: $background-body;

    p, span, h3 {
        color: $text-color;
        font-family: $default-font;
        letter-spacing: $default-letter-spacing;
    }

    .side-nav__header {
      text-transform: uppercase;
      margin: 0;
      letter-spacing: $big-letter-spacing;
      font-size: $header-font-size;
      font-weight: normal;
    }

    .side-nav__groups {
      list-style: none;
      padding: 0;
    }

    .side-nav__group {
        margin-top: 40px;
        height: 0;
        -webkit-transition: height ease-out $transition-time;
        -moz-transition: height ease-out $transition-time;
        -o-transition: height ease-out $transition-time;
        transition: height ease-out $transition-time;
        &.side-nav__group--active {
            height: auto;
        }
    }

    .side-nav__group-title {
      display: inline-block;
      margin: 0;
      font-weight: bold;
      font-size: $title-font-size;
    }

    .side-nav__group-show, .side-nav__group-hide {
      float: right;
      color: $accent-color;

      &:hover {
          color: $accent-color-hover;
          cursor: pointer;
      }
    }

    .side-nav__pages {
      list-style: none;
      padding: 0;

      > li {
        height: 94px;
        padding-left: 40px;
      }
    }

    .side-nav__page-img {
      width: $icon-button-size;
      height: $icon-button-size;
      vertical-align: middle;
      float: left;
      margin-right: 18px;
    }

    .side-nav__page-title {
      margin: 0;
      font-size: $inner-title-font-size;
    }

    .side-nav__page-description {
      margin: 0;
      font-size: $default-font-size;
      color: $gray-transparent-color;
      letter-spacing: $small-letter-spacing;
    }

    .side-nav__page-title, .side-nav__page-description {
        width: calc(100% - #{$icon-button-size} - 40px);
    }

    &.side-nav--settings {
        border: solid 1px $border-color;
        padding: 0;

        .side-nav__groups {
            margin: 0;
        }

        .side-nav__group {
            margin-top: 0;
        }

        .side-nav__pages {
          > li {
            height: auto;
            padding: 20px;
            position: relative;

            .dropdown {
                position: absolute;
                top: 20px;
                right: 20px;
                height: $icon-button-size;
            }
          }
        }

        .side-nav__group-title {
          font-style: italic;
          display: block;
          line-height: 48px;
          background-color: $background-dark-gray-color;
          border-bottom: 1px solid $border-separator-color;
        }
    }

    .ui-dialog {
        max-height: none;
    }
}
